@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:'Poppins', sans-serif}body{background-color:#E5E5E5}body.dark-mode{background-color:#000;color:#fff}body.dark-mode .cv-toolbar{background-color:rgba(66,66,66,0.712);box-shadow:none}body.dark-mode .cv-card,body.dark-mode .cv-pie-chart-container{background-color:rgba(66,66,66,0.712) !important;box-shadow:none}body.dark-mode .cv-table{background-color:rgba(66,66,66,0.712);box-shadow:none}body.dark-mode .cv-table td,body.dark-mode .cv-table th{color:#fff}body.dark-mode .cv-table tr:nth-child(even){background-color:#423F3E}.active{color:#109CF1 !important}


.switch{position:relative;display:inline-block;width:60px;height:34px}.switch input{opacity:0;width:0;height:0}.slider{position:absolute;cursor:pointer;top:0;left:0;right:0;bottom:0;background-color:#ccc;transition:.4s}.slider:before{position:absolute;content:"";height:26px;width:26px;left:4px;bottom:4px;background-color:white;transition:.4s}input:checked+.slider{background-color:#000}input:focus+.slider{box-shadow:0 0 1px #000}input:checked+.slider:before{-webkit-transform:translateX(26px);transform:translateX(26px)}.slider.round{border-radius:34px}.slider.round:before{border-radius:50%}.cv-theme-container{display:flex;align-items:center}.cv-theme-container .cv-theme-icons{display:flex;margin-right:8px;font-size:24px}

.cv-toolbar{width:100%;padding:17px 24px;background-color:#fff;display:flex;align-items:center;width:100%}.cv-toolbar .cv-brand{font-size:18px;font-weight:600;color:#109CF1;margin:0}.cv-toolbar .cv-brand a{text-decoration:none;color:inherit}@media screen and (max-width: 732px){.cv-toolbar .cv-brand{margin-left:0}}.cv-toolbar .right-side{margin-left:auto;display:flex;align-items:center}.cv-toolbar .right-side .links-container{display:flex;margin-right:36px;list-style:none}.cv-toolbar .right-side .links-container li{margin-left:12px}.cv-toolbar .right-side .links-container li a{color:inherit;text-decoration:none}.cv-toolbar .right-side .links-container li a span{font-size:medium;color:inherit;font-weight:600}.cv-toolbar .right-side .links-container li:hover{color:#109CF1}@media screen and (max-width: 732px){.cv-toolbar .right-side .links-container{margin-right:12px;flex-direction:column}}@media screen and (max-width: 732px){.cv-toolbar{padding:12px}}

